import React from 'react'
import './../home/home.css' 
import bot from '../../assets/jellyfishbot.jpg'
import qolla from '../../assets/9olla.jpg'
import carapace from '../../assets/carapace.jpg'

const Actions = () => {
  return (
    <article id="home">
    <h1 className='actionsTitle'>Actions</h1>

    <section className="section">
      <h2 className='sectionTitle'>La technologie au service de la propret des ports</h2>
      <div className='sectionDiv'>
      <img className='sectionDivIMG' src={bot} alt="Technologie" />
      <p className='sectionDivPARA'>
        Un robot, collecteur de dchets, au niveau des bassins portuaires, conu par notre groupe dexperts tunisiens, fera le mnage ! Une dmonstration est au programme, au port de la Goulette, le 16 aot 2023, dans le cadre de la journe de sensibilisation, organise par lAPIP.
      </p>
      </div>
    </section>

 


    </article>
  )
}

export default Actions

